import '../App.css';
import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import { useState, useEffect, useContext } from 'react';
import { UserContext } from './UserContext';
import { BASE_API } from './api/api';

function NavSidePage(props) {
    const [locations, setlocations] = useState([]);
    const {isAuthenticated} = useContext(UserContext);

    const fetchData = async () => {
        const locationsResponse = await fetch(`${BASE_API}/api/locations/allLocations`).then(res => res.json());
        setlocations(locationsResponse);
    };

    useEffect(() => {
      fetchData();
  }, []);

    return ( 
        <nav class="navbar navbar-expand-md navbar-light d-flex flex-md-column">
            <div className='navSideBar'>
                <ul class="navbar-nav w-100 d-flex flex-md-column text-start text-md-start">
                    <li>
                      <a class="nav-link" key='Locations' href="/allLocations">Locations</a>
                    </li>
                    <li>
                      <a class="nav-link" key='jobTitles' href="/jobTitles">Job Titles</a>
                    </li>
                    <li>
                      <a class="nav-link" key='pendingAllStaff' href="/pendingAllStaff">Pending Feed</a>
                    </li>
                    <li>
                      <a class="nav-link" key='activeAllStaff' href="/activeAllStaff">All Staff</a>
                    </li>
                    {/* <li>
                      <a class="nav-link" href="/corporateHeadquarters">Corporate Headquarters</a>
                    </li>
                    <li>
                      <a class="nav-link" href="/cambridge401">Cambridge 401</a>
                    </li>
                    <li>
                      <a class="nav-link" href="/hamilton">Hamilton 401</a>
                    </li>
                    <li>
                      <a class="nav-link" href="/london">London 401</a>
                    </li>
                    <li>
                      <a class="nav-link" href="/blackshop">Blackshop</a>
                    </li>
                    <br /> */}
                    <br />
                    {locations.map((location, i) => (
                      <li>
                      <a class="nav-link" key={i} href={location.Locations.replace(/\s+/g, '')}> {location.Locations}</a>
                    </li>
                   ))}
                   <br /> 
                    <li>
                      <a class="nav-link" key='hiddenStaffs' href="/hiddenStaffs">Hidden Staff</a>
                    </li>        
                 </ul>
            </div>
        </nav>
        
    );
}
export default NavSidePage;