import './App.css';
import React, { useContext, useMemo } from 'react';
import { useState, useEffect } from 'react';
import LogInPage from './components/LogInPage';
import PendingAllStaffs from './components/PendingAllStaffs';
import ActiveAllStaffs from './components/ActiveAllStaffs'; 
import StaffPageBody from './components/StaffPageBody';
import HiddenAllStaffs from './components/HiddenAllStaffs';
import AllLocations from './components/AllLocations';
import AllJobTitles from './components/AllJobTitles';
import Logout from './components/logout';
import Home from "./components/Home";
import Nav from './components/Nav';
import NavSidePage from './components/NavSideBar';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
// import Auth from './components/Auth';
// import { Navigate } from "react-router-dom";
import { UserContext } from './components/UserContext';
import { BASE_API } from './components/api/api';

function App() {

  const [locations, setlocations] = useState([]);
  const [isAuthenticated, setisAuthenticated] = useState(false);
  //const loggedIn = window.localStorage.getItem("isLoggedIn");
  const loggedIn = window.sessionStorage.getItem("isLoggedIn");
  console.log('from app - ' + loggedIn);

    const fetchData = async () => {
        const locationsResponse = await fetch(`${BASE_API}/api/locations/allLocations`).then(res => res.json());

        setlocations(locationsResponse);
    };

    useEffect(() => {
      fetchData();
  }, []);

  return (
      <div className='App'>
        
        <Router>
        <UserContext.Provider value={{isAuthenticated, setisAuthenticated}}>
          <Nav isAuth={loggedIn}/> 
            <div className='content'>
                <div class='container d-flex flex-column flex-md-row'>
                    {(loggedIn ? < NavSidePage /> : "") }   
                    {/* < NavSidePage isAuth={loggedIn}/> */}
                    <div className='mainContainer'>   
                      <Routes>                     
                      <Route path='/home' element={ loggedIn ? <Home /> : <LogInPage />}/>
                      {/* <Route path='/home' element={  <Home />}/> */}                   
                      <Route path='/' element={ loggedIn ? <ActiveAllStaffs /> : <LogInPage />}/>
                      {/* <Route path='/' element={ Auth.isAuthenticated() ? <ActiveAllStaffs /> : <LogInPage />}/>  */}
                      <Route path='/login' element={<LogInPage />}/>
                      <Route path='/logout' element={ <Logout/>}/>
                      <Route path='/pendingAllStaff' element={ loggedIn ?  <PendingAllStaffs /> : <LogInPage />}/>
                      <Route path='/activeAllStaff' element={  loggedIn ? <ActiveAllStaffs /> : <LogInPage />}/>
                      <Route path='/hiddenStaffs' element={loggedIn ?  <HiddenAllStaffs /> : <LogInPage />}/>
                      <Route path='/allLocations' element={loggedIn ?  <AllLocations /> : <LogInPage />}/>
                      <Route path='/jobTitles' element={loggedIn ?<AllJobTitles/> : <LogInPage />}/>
                      { loggedIn ?
                      locations.map((location, i) => (
                      <Route path={location.Locations.replace(/\s+/g, '')} key={i} element={ <StaffPageBody location={location.Locations}/> }/>
                      ))
                      : <Route path='*' element={<LogInPage />}/>
                      }
                      </Routes>
                      
                    </div>                  
                </div>         
            </div>  
            </UserContext.Provider> 
        </Router >

        
        
      </div>  
        
  );
}
export default App;
