import { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BASE_API } from './api/api';
import { Table } from 'react-bootstrap';

function PendingAllStaffs() {

    const [employees, setEmployees] = useState([]);
    //const [btnText, setBtnText] = useState('Show On Website');

    const fetchData = async () => {
        const employeesResponse = await fetch(`${BASE_API}/api/employees`).then(res => res.json());

        setEmployees(employeesResponse);
    };

    function updateemployeeSetActiveTrue(param) {
        return fetch(`${BASE_API}/api/employees/setActiveTrue/` + param)
          .then(function(result){
            return result;
        });
    }

    function updateEmployeeSetActiveFalse(param) {
        return fetch(`${BASE_API}/api/employees/setActiveFalse/` + param)
          .then(function(result){
            return result;
        });
    }

    useEffect(() => {
        fetchData();
    }, []);

    // function changeButtonText(id) {
    //     setEmployees(
    //         employees.map((item) => {
    //         if (item.EmpID === id) item.isActive = 1;
    
    //         return item;
    //       })
    //     );
    //   };
      
      function changeButtonText(id, isHidden) {
        setEmployees(
            employees.map((item) => {
            if (item.EmpID === id) 
            {
                item.isHidden = isHidden;
                if(item.isActive == 0) item.isActive = 1;
                console.log('is active - - '+item.isActive);
                console.log('is hidden - - '+item.isHidden);
            }  
            return item;
          })
        );
    };

    return (
        <div>
            <div class="d-flex flex-md-column text-start text-md-center">
                <h2>Pending Feed</h2>
                    <ul>
                        <Table striped bordered hover>
                            <thead>
                                <tr>
                                <th>Picture</th>
                                <th>First Name</th>
                                <th>Last Name</th>
                                <th>Email</th>
                                <th>Job Title</th>
                                <th>Location</th>
                                </tr>
                            </thead>
                            {employees.map((employee) => (
                            <tbody>
                                <tr>
                                    <td><img src={employee.PhotoUrl} width="105" height="90" /></td>
                                    <td>{employee.FirstName}</td>
                                    <td>{employee.LastName}</td>
                                    <td>{employee.WorkEmail}</td>
                                    <td>{employee.JobTitle}</td>
                                    <td>{employee.Location}</td>
                                    <td>
                                        <button type="button" class="btn btn-success" onClick={() => 
                                            {
                                                employee.isHidden == 0 ?
                                                updateEmployeeSetActiveFalse(employee.EmpID) : updateemployeeSetActiveTrue(employee.EmpID);
                                                changeButtonText(employee.EmpID, employee.isHidden == 0 ? 1 : 0);
                                                // setBtnText('Showing');
                                            }}>{employee.isActive == 0 && employee.isHidden == 1 ? "Show On Website" : employee.isHidden == 1 ? "Hidden" :  "Showing"}</button>
                                            {/* <br/><br/>
                                            <button type="button" class="btn btn-success" onClick={() => 
                                                                            {
                                                                                employee.isHidden == 0 ?
                                                                                updateEmployeeSetActiveFalse(employee.EmpID) : updateemployeeSetActiveTrue(employee.EmpID);
                                                                                changeButtonText(employee.EmpID, employee.isHidden == 0 ? 1 : 0);
                                                                                // setBtnText('Showing');
                                                                            }}>{employee.isHidden == 1 ? "Hidden" : "Showing"}</button> */}
                                    </td>                                                         
                                </tr>
                            </tbody>
                            ))}
                        </Table> 
                </ul>
            </div>
        </div>
    );
}
export default PendingAllStaffs;